* {
  margin: 0;
  padding: 0;
  outline: 0;
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

html,
body,
#root {
  height: 100%;
}

.container {
    min-height: 100vh;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}
.container-buttons-menu{
    display:flex;
    flex-direction: row;
}
.container-buttons-menu button{
    width:33.3vw;
    border:none;
    outline: none;
    border-right: 1px black solid;
    border-bottom: 1px black solid;
    padding: 10px;
    cursor: pointer;
    background: #E0DCE4;
}
.container-buttons-menu button:hover{
    border-bottom: 1px #53BCAD solid;
}
.container-buttons-menu-af{
    display:flex;
}
.table{
    border: 1px solid black;
}
.headerTable {
    display: flex;
}
.cellTable {
    border: 1px solid black;
    width: 60px;
    height: 25px;
    text-align: center;
    cursor: pointer;
}
.containerBody {
    display: flex;
}
.lineTable {
    display:flex;   
}
.headerLeftTable {
    display: flex;
    flex-direction: column;
}
input[type='file'] {
    display: none;
}
label {
    cursor:pointer;
    border: 1px black solid;
    background: #E0DCE4;
}
